<template>
  <div class="flex h-full min-h-[500px] w-full flex-col">
    <GeneralForm
      :heading="`Add Companies to ${productData?.title}`"
      :showCloseButton="true"
      @close-pressed="handleClose"
      @form-submitted="handleSubmit"
      :is-form-submitting="isFormSubmitting"
      class="flex-1"
    >
      <template #form-body>
        <CompanySearchDropdown
          :dropdown-label="'Companies'"
          :dropdown-id="'companies'"
          :companies="formData.companies"
          :is-multi-select="true"
          :black-listed="blackListed"
          @on-companies-update="updateCompanies"
          :allow-create-new="true"
        />
      </template>
    </GeneralForm>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  props: {
    productData: {
      type: Object,
      required: true,
    },
  },
  components: {
    GeneralForm: () => import("@/components/General/Form.vue"),
    CompanySearchDropdown: () =>
      import("@/components/Utility/CustomDropdowns/CompanySearch.vue"),
  },
  computed: {
    ...mapGetters({
      user: "auth/getUser",
    }),
    blackListed() {
      return this.productData?.companies?.map((item) => item._id) || [];
    },
  },
  data() {
    return {
      formData: {
        companies: [],
      },
      isFormSubmitting: false,
    };
  },
  methods: {
    ...mapActions({
      addCompaniesToProduct: "category/addCompaniesToProduct",
    }),

    async handleSubmit() {
      this.isFormSubmitting = true;
      let companyIds = this.formData?.companies
        ?.filter((item) => !item?.isNew)
        ?.map((item) => item?._id);
      let newCompanies = this.formData?.companies
        ?.filter((item) => item?.isNew)
        ?.map((item) => ({
          companyName: item?.display_name,
          addUser: this.user?._id,
          origin: "QUICK_ADD",
        }));
      let payload = {
        id: this.productData?._id,
        body: {
          companyIds,
          newCompanies,
        },
      };
      const res = await this.addCompaniesToProduct(payload);
      if (res?.success) {
        this.$bus.$emit("fetch-product-details", { id: this.productData?._id });
        this.handleClose();
      }
      this.isFormSubmitting = false;
      this.handleClose();
    },
    handleClose() {
      this.$bus.$emit("add-companies-to-product-modal", { showModal: false });
    },
    updateCompanies(companies) {
      this.formData.companies = companies;
    },
  },
};
</script>
